.auth__title {
  margin: 0;
  padding: 0 0 80px;
  grid-area: title;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  text-align: center;
}

@media screen and (min-width: 769px) {
  .auth__title {
    padding: 40px 0;
    text-align: start;
  }
}

.auth__title_type_protfile {
  padding: 70px 0 83px;
}

@media screen and (min-width: 600px) {
  .auth__title_type_protfile {
    padding: 235px 0 100px;
  }
}


@media screen and (min-width: 769px) {
  .auth__title_type_protfile {
    padding: 74px 0 123px;
    text-align: center;
  }
}

@media screen and (min-width: 1280px) {
  .auth__title_type_protfile {
    padding: 75px 0 122px;
  }
}
