.auth__form {
  min-height: 100vh;
  margin: auto;

  color: #fff;
  font-family: Inter;
}

.auth__form_type_profile-error-messages {
  display: grid;
  justify-items: center;
  align-items: end;
  font-size: 11px;
  font-weight: 400;
  padding-bottom: 20px;
  font-style: normal;
}

.auth__form_type_profile-error-message {
  color: #ff004c;
  display: block;
}

.auth__form_type_profile {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: calc(100vh - 74px);
}

@media screen and (max-width: 900px) {
  .auth__form_type_profile{
    max-height: 445px;
  }
  
}
