.header {
  display: grid;
  box-sizing: border-box;
  justify-items: center;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-weight: 500;
}

.header_type_main {
  background-color: #073042;
}

.header_type_auth {
  padding-top: 5px;
  min-height: 144px;
  grid-template-columns: auto;
  justify-content: center;
}
@media screen and (min-width: 769px) {
  .header_type_auth {
    padding-top: 0;
    min-height: 0;
    width: 100%;
    max-width: 396px;
    height: 144px;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin: auto;
  }
}

.header__container {
  /* align-items: center; */
  box-sizing: border-box;
  /* display: grid; */
  /* grid-template-columns: auto 1fr; */
  display: flex;
  justify-content: space-between;
  align-items: center;


  min-height: 74px;
  padding: 0 16px;
  position: relative;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .header__container {
    padding: 0 30px;
  }
}

@media screen and (min-width: 1280px) {
  .header__container {
    justify-self: center;
    padding: 0 70px;
    max-width: 1280px;
  }
}

@media screen and (min-width: 769px) {
  .header__container_type_auth {
    width: 396px;
    padding: 0;
    min-height: 0;
  }
}
